.container {
  overflow: hidden;
  height: 100vh;
  width: 100vw;
}
.nav {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.45);
  background-color: #0d3a59;
  color: #fff7ec;
}
.content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.icon {
  display: flex;
  align-items: center;
  margin-left: 1em;
}
.links {
  margin-right: 1em;
}



