.btn {
  background-color: transparent;
  border: 1px solid #0d3a59;
  padding: .5em 2em;
  color: #0d3a59;
  text-align: center;
  cursor: pointer;
  line-height: 1.15;
  max-width: 100%;
  transition: all 0.1s ease-in-out;
  margin: 1em;
}
.btn:hover {
  color: #fff;
  background-color: #0d3a59;
}
.btnDark {
  background-color: #0d3a59;
  border: 1px solid #fff7ec;
  padding: .5em 2em;
  color: #fff7ec;
  text-align: center;
  cursor: pointer;
  line-height: 1.15;
  max-width: 100%;
  transition: all 0.1s ease-in-out;
  margin: 1em;
  border-radius: 6px;
}
.btnDark:hover {
  color: #0d3a59;
  background-color: #fff7ec;
  border: 1px solid #0d3a59;
}



