.container {
  display: flex;
  align-items: center;
  background-color: #0d3a59;
  border-bottom: 1px solid #0d3a59;
  color: #fff7ec;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
}
@media only screen and (min-width: 64rem) {
  .container {
    border-bottom: none;
  }
}
.item {
  flex: 1 1 auto;
}
.searchIcon {
  margin-right: 5px;
}
.input {
  padding: 0.1em;
  background: transparent;
  border: none;
  border-bottom: 1px solid #fff7ec;
  margin: .5em;
  color: #fff7ec;
  text-shadow: 0 0 0 #f2f2f2;
  -webkit-transition: all 250ms;
  transition: all 250ms;
  font-family: 'Rubik', sans-serif;
  text-decoration: none;
  font-size: .8em;
  letter-spacing: .1em;
}
.input::placeholder {
  color: #fff7ec;
}
.search {
  width: 20px;
  height: 20px;
  color: #f63854;
}
.menu {
  position: fixed;
  width: 50vw;
  height: 100vh;
  transform: translateX(110vw);
}
@media only screen and (min-width: 64rem) {
  .menu {
    flex: 0 1 auto;
    position: static;
    width: auto;
    height: auto;
    transform: translateX(0vw);
  }
}



